html {
  scroll-behavior: smooth;
  height: 100%; }

body {
  position: relative;
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-size: 16px;
  line-height: 21px;
  font-weight: 500;
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  color: #1d1613;
  background-color: #ffffff;
  height: 100%; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
  background-position: 0 0; }

.main-header {
  max-width: 1920px;
  padding: 29px 5.2%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background-color: #141414; }

@media (max-width: 1600px) {
