  height: 60px;
  margin-right: 13px;
  border: 1px solid #ffffff;
  -webkit-transform: skew(-20deg);
          transform: skew(-20deg);
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

.main-nav__phone a {
