.main-nav {
  position: relative;
  width: 100%;
}

.main-nav__wrapper {
  display: flex;
  width: 100%;
}

.main-nav__toggle {
  display: none;
  height: 60px;
  width: 93px;
  margin-right: 60px;
  border: none;
  background-color: transparent;
}

.main-nav__toggle div {
  height: 5px;
  margin-top: 10px;
  border-radius: 5px;
  background-color: #ffffff;
}

.main-nav__toggle div:first-child {
  margin-top: 0;
  width: 50%;
}

.main-nav__toggle div:nth-child(3) {
  width: 70%;
}

.main-nav__logo-wrapper {
 height: 60px;
}

.main-nav__logo-wrapper a {
  display: block;
  height: 100%;
}

.main-nav__list {
  margin: 0;
  padding: 0;
  margin-left: 4.42%;
  max-width: 830px;
  display: flex;
  flex-wrap: wrap;
  list-style: none;
}

.main-nav__list li {
  margin-right: 42px;
}

.main-nav__list li:first-child {
  margin-left: 0;
}

.main-nav__list a {
  display: block;
  height: 100%;
  padding: 20px 0;
  font-family: $montserrat;
  font-size: 14px;
  line-height: 21px;
  font-weight: 700;
  text-transform: uppercase;
  color: #ffffff;
  text-decoration: none;
  box-sizing: border-box;
}

.main-nav__user-wrapper {
  margin-left: auto;
  display: flex;
}

.main-nav__phone {
  position: relative;
  width: 93px;
  height: 60px;
  margin-right: 13px;
  border: 1px solid #ffffff;
  transform: skew(-20deg);
  box-sizing: border-box;
}

.main-nav__phone a {
  display: block;
  height: 100%;
  background-color: transparent;
}

.main-nav__phone svg {
  margin-left: 36px;
  margin-top: 20px;
}

.main-nav__application {
  position: relative;
  background-color: #C2AB81;
  min-width: 283px;
  height: 60px;

  transform: skew(-20deg);
}

.main-nav__application a {
  position: relative;
  display: block;
  font-size: 12px;
  line-height: 18px;
  font-weight: 700;
  height: 100%;
  padding: 19px 5px;
  color: white;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  box-sizing: border-box;
}

.main-nav__application a::after {
  position: absolute;
  content: '';
  top: 0;
  right: -15px;
  width: 10px;
  height: 100%;
  background-color: #ffffff;
}

@media (max-width: 1600px) {
  .main-nav {
    position: initial;
  }

  .main-nav__toggle {
    display: block;
  }

  .main-nav--closed .main-nav__list {
    display: none;
  }

  .main-nav--opened .main-nav__list {
    display: flex;
    flex-direction: column;
    position: absolute;
    left: 0;
    top: 118px;
    margin: 0;
    padding: 54px 5.2%;
    background-color: $bg-color;
    width: 100%;
    max-width: 100%;

  }

  .main-nav__list li {
    margin-right: 42px;
  }

  .main-nav__list li:first-child {
    margin-left: 0;
  }

  .main-nav__list a {
    color: $bg-color;
    text-shadow: 1px 0 0 #808080,
    -1px 0 0 #808080,
    0 1px 0 #808080,
    0 -1px 0 #808080;
  }
}
